<div class="hello"><div class="ui segment container"> <h2 class="ui header"><i class="bell icon"></i>通知</h2><div class="ui list"><div class="left aligned item" v-for="(n, idx) in sortedNotifications" :key="idx" v-show="!n.hidden" :class="{hidden: n.hidden}">{{n.date.split('T')[0]}}：{{ n.text }} {{n.hidden}}<div class="filler"></div><div class="ui buttons"><router-link class="ui green button" :to="n.route"><i class="eye icon"></i>查看</router-link><button class="ui basic red button" @click.prevent="hideNotification(n.nid)"> <i class="delete icon"></i>刪除</button></div></div></div></div></div>